const app = getApp();
export default {
	data() {
		return {
			addressName: '在地图选择',
			addressData: {}
		}
	},
	onLoad(option){
		let title = '新增收货地址';
		if(option.type==='edit'){
			title = '编辑收货地址'
			
			this.addressData = JSON.parse(option.data)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e){
			this.addressData.state = e.detail.value;
		},
		
		//地图选择地址
		chooseLocation(){
			console.log('chooseLocation')
			let _this = this;
			uni.chooseLocation({
				success: (data)=> {
					console.log(data);
					// this.addressData.addressName = data.name;
					_this.addressData.address = data.address;
					
					console.log("_this.addressData");
					console.log(_this.addressData);
					_this.$forceUpdate();
				}
			})
			
			
		},
		
		//提交
		confirm(){
			let _this = this;
			let data = _this.addressData;
			if(!data.name){
				_this.$api.msg('请填写收货人姓名');
				return;
			}
			if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)){
				_this.$api.msg('请输入正确的手机号码');
				return;
			}
			if(!data.address){
				_this.$api.msg('请在地图选择所在位置');
				return;
			}
			if(!data.home_number){
				_this.$api.msg('请填写门牌号信息');
				return;
			}
			console.log(data);
			if(_this.manageType=='edit'){
				app.request(_this.$Url + "/clmsj/Address/edit_address", data).then(res=>{
					if (res.data.code == 0) {
						uni.showToast({
							title:'修改成功'
						})
						//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
						this.$api.prePage().refreshList(data, this.manageType);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					}else{
						uni.showToast({
							title:'修改失败'
						})
					}
				});
			}else{
				app.request(_this.$Url + "/clmsj/Address/add_address", data).then(res=>{
					if (res.data.code == 0) {
						uni.showToast({
							title:'添加成功'
						})
						//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
						this.$api.prePage().refreshList(data, this.manageType);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					}else{
						uni.showToast({
							title:'添加失败'
						})
					}
				});
			}
			
			
		},
	}
}
